@charset "UTF-8";

//----------------------------------------------
$blue: #1498d5;
$brown: #584E4A;
$whitey: #f0f0f0;

$duration: .2s;
$distance: 8px;
$easeOutBack: cubic-bezier(0.175, 0.885, 0.320, 1.275);

.navbar-default {
  min-height: 90px;
  margin-bottom: 0;
  background-color: rgba(255, 255, 255, .95);
  border-bottom: 0;
  transition: 300ms all ease;

  &.fixed {
    min-height: 74px;
    transition: 300ms all ease;

    .navbar-nav {
      margin-top: 14px;
    }

    .logo {
      width: 60px;
    }
  }

  .logo {
    margin-top: 0;
    margin-bottom: 0;
  }

  .navbar-nav {
    margin-top: 22px;

    > li {
      > a {
        position: relative;
        padding: 10px 20px;
        font-size: 17px;
        font-weight: bold;
        color: #1498d5;

        &:before,
        &:after {
          content: '';
          position: absolute;
          bottom: 2px;
          left: 0; right: 0;
          height: 2px;
          background-color: $blue;
        }
        &:before {
          opacity: 0;
          transform: translateY(- $distance);
            transition: transform 0s $easeOutBack, opacity 0s;
            }
        &:after {
          opacity: 0;
          transform: translateY($distance/2);
          transition: transform $duration $easeOutBack, opacity $duration;
        }
        &:hover,
        &:focus {
          &:before,
          &:after {
            opacity: 1;
            transform: translateY(0);
          }
          &:before {
            transition: transform $duration $easeOutBack, opacity $duration;
          }
          &:after {
            transition: transform 0s $duration $easeOutBack, opacity 0s $duration;
          }
        }
      }
    }
  }
}

.navbar-brand {
  height: 62px;
}

//----------------------------------------------
.menu-trigger {
  position: absolute;
  top: 3px;
  right: 0;
  width: 40px;
  height: 32px;
  transition: all .3s;

  @media (max-width: $screen-xs-max) {
    display: inline-block;
  }

  &,
  &:hover,
  &:focus,
  &:active {
    background-color: transparent !important;
    border: 0;
    outline: 0;
  }

  .icon-bar {
    position: absolute;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 4px;
    background-color: #888;
    border-radius: 4px;
    transition: all .3s;

    &:nth-of-type(1) {
      top: 0;
    }

    &:nth-of-type(2) {
      top: 14px;
    }

    &:nth-of-type(3) {
      bottom: 0;
    }

    & + .icon-bar {
      margin-top: 0;
    }
  }

  &.active {
    .icon-bar {
      &:nth-of-type(1) {
        transform: translateY(14px) rotate(-45deg);
      }

      &:nth-of-type(2) {
        opacity: 0;
      }

      &:nth-of-type(3) {
        transform: translateY(-14px) rotate(45deg);
      }
    }
  }
}
