// }

//----------------------------------------------
.btn-multiline {
  white-space: normal;
}

//----------------------------------------------
.btn {
  @include transition(all $hover-transition-speed ease);

  &.arrow-blue-left {
    display: inline-block;
    padding-left: 30px;
    background-image: url("../img/arrow/arrow-blue-left.png");
    background-position: 5% 50%;
    background-repeat: no-repeat;
  }

  &.arrow-blue-right {
    display: inline-block;
    padding-right: 30px;
    background-image: url("../img/arrow/arrow-blue-right.png");
    background-position: 95% 50%;
    background-repeat: no-repeat;
  }
}

//----------------------------------------------
.btn-orange {
  @include button-variant(#fff, #fd7e14, #fd7e14);

  &.icon-arrow-right-yellow {
    &:after {
      display: inline-block;
      width: 10px;
      height: 12px;
      vertical-align: middle;
      background: url("../img/icon/arrow-right-yellow.png") 0 0 no-repeat;
      content: "";
    }
  }

  &:hover,
  &:focus {
    color: #fff !important;
    background-color: #fc5e0b !important;
    border-color: #fc5e0b;
  }

  &.btn-sm {
    padding: 5px 10px 2px;
    font-size: 14px;
    line-height: 1.4;

    @media (max-width: $screen-sm-max) {
      font-size: 13px;
    }
  }
}
