@charset "UTF-8";

//----------------------------------------------
.title-double-dash {
  width: 500px;
  margin-right: auto;
  margin-left: auto;
  font-size: 30px;
  font-weight: bold;
  text-align: center;

  @media (max-width: $screen-xs-max) {
    width: 100%;
  }

  .cell {
    padding-right: 15px;
    padding-left: 15px;
  }

  .text {
    width: 260px;
  }

  .dash {
    position: relative;

    &:before,
    &:after {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 100%;
      height: 8px;
      margin-top: auto;
      margin-bottom: auto;
      border-top: 2px solid #1498d5;
      border-bottom: 2px solid #1498d5;
      content: "";
    }

    &:before {
      left: 0;
    }

    &:after {
      right: 0;
    }
  }
}

.title-border-left-blue {
  padding-left: 15px;
  font-size: 25px;
  border-left: 5px solid #1498d5;
}
